.footer_container {
  border-left: solid 1px black;
  border-right: solid 1px black;
  border-bottom: solid 1px black;
  background-color: white;
}

.footer-body {
  display: flex;
  width: 100%;
  padding-inline: 80px;
  align-items: center;
}
@media (max-width: 900px) {
  .footer-body {
    flex-direction: column;
    padding: 0;
  }
}

.footer-img {
  padding-block: 35px;
  padding-inline-end: 80px;
  border-right: solid 1px black;
}
@media (max-width: 900px) {
  .footer-img {
    border: inherit;
    padding: 20px;
  }
}

.footer-img img {
  width: 100%;
}

.footer_body-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 900px) {
  .footer_body-nav {
    flex-direction: column-reverse;
    text-align: center;
    border: solid 1px black;
  }
}

.copyright {
  padding-inline-start: 80px;
}
@media (max-width: 900px) {
  .copyright {
    padding-inline-start: 0;
    padding-block: 20px;
  }
}

.copyright p {
  font-size: 13px;
  font-family: Nunito Sans, sans-serif;
}

.menu-list {
  display: flex;
  gap: 50px;
}
@media (max-width: 900px) {
  .menu-list {
    padding-block-start: 20px;
  }
}

.menu_list-link {
  font-size: 13px;
  font-weight: 700;
}